body {
  background: #fafafa
}

.info, .tech {
  text-align: center;
}

.info header, .tech header {
  color: #c8c8c8;
  letter-spacing: 3px;
  font-size: 12px;
  margin-bottom: 8px;
}

.info header .titulo, .tech header .titulo {
  margin-top: .75rem;
  text-transform: uppercase
}

.info header i, .tech header i {
  font-size: 2rem;
}

.info .info-text {
  padding: 0 1.5rem 0 1.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.info .info-mail {
  padding: 0 1.5rem 0 1.5rem;
}

.info .info-text i {
  margin: 0 .3rem 0 0;
}

.info .info-text a {
  margin-right: .5rem
}

.tech header {
  color: white;
}



